import { AppComponent } from './../app.component';
import { User } from './../Interfaces/user.interface';
import { Component, OnInit } from '@angular/core';
import { HttpServices } from '../Services/http.services';
import { AuthService } from "angular2-social-login";

import { Router } from '@angular/router';
declare var swal: any;

@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.css']
})
export class RegistrationComponent extends AppComponent implements OnInit {
  errormsg: string;
  exist: any;
  public genderss = [
    { value: 'Female', display: 'Female' },
    { value: 'Male', display: 'Male' }
  ];
  constructor(public router: Router, public _auth: AuthService, public httpservice: HttpServices) {
    super();
  }
  public showcityddl: boolean = false;
  public Country = [];
  public City = [];

  public phone_no: string;
  ngOnInit() {

    this.phone_no = "+92";
    this.httpservice.GetSinglehttp("api/Country")
      .subscribe(
      res => {
        this.Country = res;
        console.log(res)
      }
      )

  }

  getcountryId(countryId) {
    this.showcityddl = true;
    this.httpservice.GetSinglehttp("api/City/" + countryId)
      .subscribe(
      res => {
        this.City = res;
      }
      )
  }
  activeuser(u_email) {
    var member = { email: u_email }
    this.httpservice.Posthttp("api/activeuser", member).subscribe(
      res => {
        console.log("Account Activated!");
      })
  }
  SignUp(Users: any) {

    var member = {
      email: Users.email,
      gender: Users.gender,
      name: Users.name,
      password: Users.password,
      phone_no: Users.phone_no,
      cityID: Users.cityID
    }
    if (member.cityID == undefined) {
      swal(
        'Missing City...',
        "Please Select Country or City",
        'error'
      )
    }
    if (member.gender == undefined) {
      swal(
        'Missing Gender...',
        "Please Select Gender",
        'error'
      )
    }
    if (member.gender != undefined && member.cityID != undefined) {
      var that = this;
      this.httpservice.Posthttp("api/SignUp", member)
        .subscribe(
        res => {
          // console.log(res);
          // console.log(res.message);
          swal({
            title: 'Verification!',
            text: 'Go to mail and verify your email!',
            timer: 2000
          }).then(
            function () { },
            // handling the promise rejection
            function (dismiss) {
              if (dismiss === 'timer') {
                console.log('I was closed by the timer')

              }
            }
            )
          this.router.navigate(["/login"]);

        }, reserror => {
          var error = JSON.parse(reserror._body);
          this.errormsg = error.message;
          swal(
            'Oops...',
            this.errormsg,
            'error'
          )
          //console.log(reserror)

        }

        )
    }


  }


 




}



