import { AppGlobals } from './../app.global';
import { User } from './../Interfaces/user.interface';
import { Router } from '@angular/router';
import { Localstorage } from './../router-guard/StorageService.service';
import { HttpServices } from './../Services/http.services';
import { Component, OnInit } from '@angular/core';
declare var swal: any;

@Component({
  selector: 'app-myshops',
  templateUrl: './myshops.component.html',
  styleUrls: ['./myshops.component.css']
})
export class MyshopsComponent implements OnInit {
  userdata: any;
  shopinfo = []
  // shopitems = [];
  message: string;
  VendorId: any;
  shopitems: any;
  constructor(private _global: AppGlobals, private router: Router, private localstorages: Localstorage, private httpservice: HttpServices) { }

  ngOnInit() {
    //   var VendorId = localStorage.getItem("VendorId");
    var that = this;
    this.VendorId = this.localstorages.getsingleitems("VendorId")


    if (this.VendorId != undefined) {

      console.log(this.VendorId)
      that.httpservice.GetSinglehttpwithtoken("api/shopinfo/" + this.VendorId)
        .subscribe(

        res => {

          console.log(res);
          that.shopinfo = res;

        }
        )
    } else {

      this.message = "You have no Vendor Account";
      swal(
        'Oops...',
        this.message,
        'error'
      )

    }
  }

  getallProduct(shopid, categoryid) {
    this.userdata = this.localstorages.getarrayitems("Userdata")

    this.httpservice.GetSinglehttpwithtoken("api/allShopcatitems/" + shopid + "/" + categoryid + "/" + this.userdata[0].UserId)
      .subscribe(
      res => {
        console.log(res);
        if (res.lenth > 0) {
          this._global.shopitems = res;
          this.localstorages.setarrayitems(res, "shopitems");
        } else {
          this.message = "You have no Items ";
          swal(
            'Oops...',
            this.message,
            'error'
          )
        }

        // this.router.navigate(['/myAccount/shopitems']);
      })
  }
  activeallProduct(shopid, categoryid) {
    console.log(categoryid + " " + shopid)
    var userdata = this.localstorages.getarrayitems("Userdata")
    this.httpservice.GetSinglehttpwithtoken("api/activeallShopcatitems/" + shopid + "/" + categoryid + "/" + userdata[0].UserId)
      .subscribe(
      res => {
        console.log(res);
        console.log(res);
        if (res.lenth > 0) {
          this._global.shopitems = res;
          this.localstorages.setarrayitems(res, "shopitems");
        } else {
          this.message = "You have no Deactivate Item";
          swal(
            'Oops...',
            this.message,
            'error'
          )
        }
      })
  }
  deactiveallProduct(shopid, categoryid) {
    var userdata = this.localstorages.getarrayitems("Userdata")
    this.httpservice.GetSinglehttpwithtoken("api/deactiveallShopcatitems/" + shopid + "/" + categoryid + "/" + userdata[0].UserId)
      .subscribe(
      res => {
        console.log(res);
        if(res.lenth > 0){
          this._global.shopitems = res;
          this.localstorages.setarrayitems(res, "shopitems");
        }else{
          this.message = "You have no Activate Item";
          swal(
            'Oops...',
            this.message ,
            'error'
          )
        }
        //  this.router.navigate(['/myAccount/shopitems']);
      })

  }

}



