import { AppGlobals } from './../app.global';
import { Router } from '@angular/router';
import { AppComponent } from './../app.component';
import { HttpServices } from './../Services/http.services';
import { Localstorage } from './../router-guard/StorageService.service';
import { Component, OnInit, Input, ElementRef, EventEmitter } from '@angular/core';
import { CompleterService, CompleterData } from 'ng2-completer';
declare var CryptoJS: any;
declare var $;
declare var swal;
@Component({
  selector: 'app-adcart',
  templateUrl: './adcart.component.html',
  styleUrls: ['./adcart.component.css']
})
export class AdcartComponent implements OnInit {
  cartdata: any;
  showeditable: boolean = false;
  rowid: any;
  sum = 0;
  constructor(private _global: AppGlobals, private element: ElementRef, private router: Router, private localstorages: Localstorage, private httpservice: HttpServices) { }

  ngOnInit() {
    var userdata = this.localstorages.getarrayitems("Userdata")
    this.cartdata = this.localstorages.getarrayitems("AdCartdata");
    if (userdata == undefined) {
      this.router.navigate(["/login"]);
    }
   
    // if (userdata != undefined) {
    //   this.httpservice.GetSinglehttp("api/getAdCartdata" + "/" + userdata[0].UserId)
    //     .subscribe(
    //     res => {
    //       this.cartdata = res.AdCartdata;
    //       console.log(this.cartdata)
    //     });
    // }
    this.gettotal()
  }
  edit(val) {
    this.rowid = val;
  }

  gettotal() {
    this.cartdata = this.localstorages.getarrayitems("AdCartdata");

    for (var i = 0; i < this.cartdata.length; i++) {
      this.sum = this.sum + parseInt(this.cartdata[i].Price);

    }
    return this.sum;
  }

  getSum(): number {
    this.cartdata = this.localstorages.getarrayitems("AdCartdata");
    let sum = 0;
    this.cartdata.forEach(model => {
      sum += parseInt(this.cartdata.price);
    });
    return sum;
  }


  popcartitem(index) {
    // alert(index)
    this.cartdata = this.localstorages.getarrayitems("AdCartdata");

    this.cartdata.splice(index, 1);
    this.localstorages.setarrayitems(this.cartdata, 'AdCartdata');
  }

  checkout() {
    this.cartdata = this.localstorages.getarrayitems("AdCartdata");
    this.httpservice.Posthttp("api/AdCart", this.cartdata)
      .subscribe(res => {
        console.log(res)
         
        swal({
          type: 'success',
          title: 'Shopping!',
          html: 'Thanks For Ordering items,ShopOmania will contact You Soon'
        })
        localStorage.removeItem("AdCartdata");
       this.router.navigate(["/main"]);
      });
  }
  itemdetail(categegoryid, adid, ShopId) {
 //   alert(categegoryid + " " + adid + ShopId)
    if (ShopId != null && ShopId != undefined) {
      this.httpservice.GetSinglehttp('api/getitemdetail/' + categegoryid + '/' + adid)
        .subscribe(
        ress => {
          this._global.item_detail = ress

          this.router.navigate(["allresult/Product", adid]);
        });
    } else {
      this.httpservice.GetSinglehttp('api/getitemdetailforopenpost/' + categegoryid + '/' + adid)
        .subscribe(
        ress => {
          this._global.item_detail = ress

          this.router.navigate(["allresult/Product", adid]);
        });
    }

  }

  getSums(index: number, items) {
    let sum = 0;
    console.log(items)
    // for (let i = 0; i < items.length; i++) {
    //   sum += items[i][index].Price;
    // }
    // return sum;
  }


}



