import { Localstorage } from './../router-guard/StorageService.service';
import { AppComponent } from './../app.component';
import { HttpServices } from './../Services/http.services';
import { Component, OnInit, ElementRef, Input, ViewChild } from '@angular/core';
import { AuthService } from "angular2-social-login";
import { LocalStorageService } from 'angular-2-local-storage';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { FileUploader } from 'ng2-file-upload/ng2-file-upload';
import { Http, Response } from '@angular/http';
import { AppGlobals } from './../app.global';
import * as jsPDF from 'jspdf';
declare var swal: any;
@Component({
  selector: 'app-itemdetail',
  templateUrl: './itemdetail.component.html',
  styleUrls: ['./itemdetail.component.css']
})
export class ItemdetailComponent extends AppComponent implements OnInit {
  itemresult: any;
  subscribes: boolean;
  public userdata = [];
  constructor(private router: Router, private _global: AppGlobals, private httpservice: HttpServices, private localstorages: Localstorage) {
    super();

    this.itemresult = this._global.item_detail;
  }
  @ViewChild('content') content: ElementRef;
  ngOnInit() {
    window.scrollTo(0, 0)
    this.itemresult = this._global.item_detail;
    this.checksubscription(this.itemresult[0].ShopID);
    this.userdata = this.localstorages.getarrayitems("Userdata");
  }
  checksubscription(ShopId) {
    var userdata = this.localstorages.getarrayitems("Userdata")
    var shops = this.localstorages.getarrayitems("subcribershoped");

    if (shops != null && userdata != null) {
      for (var i = 0; i < shops.length; i++) {
        if (shops[i].ShopID == ShopId) {
          if (shops[i].UserID == userdata[0].UserId) {
            //    alert("true")
            this.subscribes = true;
          } else {
            //  alert("false")
            this.subscribes = false;
          }
          break;
        }
      }
    } else {
      this.subscribes = false;
    }

  }
  viewotherads(categoryid, vendorid) {
   // alert(categoryid + " " + vendorid)
    this.httpservice.GetSinglehttp('api/ShopAdsitem/' + categoryid + "/" + vendorid)
      .subscribe(
      ress => {

        this._global.items = ress;
        console.log(this._global.items)

        this.localstorages.setarrayitems(ress, 'allitemsofshop');
        this.router.navigate(["/allresult/shopads"]);
      });

  }
  subscribe(ShopId) {
    //alert(ShopId)
    var userdata = this.localstorages.getarrayitems("Userdata")
    if (userdata != null) {
      var postdata = { ShopID: ShopId, UserID: userdata[0].UserId };
      this.httpservice.postSinglehttpwithtonken("api/SubscribeShop", postdata)
        .subscribe(
        ressss => {
          this.subscribes = true;
          this.httpservice.GetSinglehttp("api/GetShops/" + userdata[0].UserId)
            .subscribe(
            resss => {
              this.localstorages.setarrayitems(resss, 'subcribershoped');
            })
        });

    } else {
      swal(
        'Oops...',
        'You have to login for Subscription!',
        'error'
      )
      this.router.navigate(['/login'])
      // swal(
      //   'Oops...',
      //   this.errormsg,
      //   'error'
      // )
    }



    // if (userdata != undefined) {
    //   var postdata = { ShopID: ShopId, UserID: userdata[0].UserId };
    //   this.httpservice.postSinglehttpwithtonken("api/SubscribeShop", postdata).subscribe(
    //     res => {

    //     })
    // } else {

    //   this.router.navigate(["/login"]);
    // }
  }

  adtocart(ShopID, AdId, Email, Image, Title, Price, CategoryId) {
    if (this.userdata != undefined || this.userdata != null) {


      var AdCart = this.localstorages.getarrayitems("AdCartdata");
      var data = {
        ShopID: ShopID,
        Email: Email,
        Title: Title,
        Price: Price,
        Image: Image,
        Order_Quantity: 1,
        CardName: 'BUY',
        Confirmitem: true,
        AdIdforcart: AdId,
        UserID: this.userdata[0].UserId,
        CategoryId: CategoryId
      };
      console.log(data)
      if (AdCart == undefined) {
        var datas = [];
        datas.push(data);
        this.localstorages.setarrayitems(datas, 'AdCartdata');
      } else {
        AdCart.push(data);
        this.localstorages.setarrayitems(AdCart, 'AdCartdata');
      }

      swal({
        type: 'success',
        title: 'Added!',
        html: 'Item Added in your Cart!'
      })
      var AdCart = this.localstorages.getarrayitems("AdCartdata");
      console.log(AdCart)
    } else {
      swal(
        'Oops...',
        'Please Login For Shopping!',
        'error'
      )
    }

  }

  Adtowish(ShopID, AdId, Email, Image, Title, Price, CategoryId, description) {

    if (this.userdata != undefined || this.userdata != null) {
      // var Adwish = this.localstorages.getarrayitems("AdWishdata");
      var data = [];
      data = [{
        ShopID: ShopID,
        Email: Email,
        Title: Title,
        Price: Price,
        Image: Image,
        description: description,
        AdIdforcart: AdId,
        UserID: this.userdata[0].UserId,
        CategoryId: CategoryId
      }]
      console.log(data)

      this.httpservice.Posthttp("api/Adwishitem", data)
        .subscribe(res => {
          console.log(res)

          swal({
            type: 'success',
            title: 'Added!',
            html: 'Your selected item in your Wish List'
          })


        });
      // if (Adwish == undefined) {
      //   var datas = [];
      //   datas.push(data);
      //   this.localstorages.setarrayitems(datas, 'AdWishdata');
      // } else {
      //   Adwish.push(data);
      //   this.localstorages.setarrayitems(Adwish, 'AdWishdata');
      // }

      // swal({
      //   type: 'success',
      //   title: 'Added!',
      //   html: 'Item Added in your WishList!'
      // })
      // var Adwish = this.localstorages.getarrayitems("AdWishdata");
      // console.log(Adwish)
    } else {
      swal(
        'Oops...',
        'Please Login For Wishlist!',
        'error'
      )
    }

  }

  downloadpdf() {
    let doc = new jsPDF();
    let specialElementHandlers = {
      '#editor': function (element, renderer) {
        return true;
      }
    };

    let content = this.content.nativeElement;
    doc.fromHTML(content.innerHTML, 0, 0, {
      'width': 190,
      'elementHandlers': specialElementHandlers
    }, function (bla) {
      doc.save('saveInCallback.pdf');
    }, 0);
    // doc.save("test.pdf");
  }
}



