import { AppGlobals } from './../app.global';
import { Router } from '@angular/router';
import { AppComponent } from './../app.component';
import { HttpServices } from './../Services/http.services';
import { Localstorage } from './../router-guard/StorageService.service';
import { Component, OnInit, Input, ElementRef, EventEmitter } from '@angular/core';
import { CompleterService, CompleterData } from 'ng2-completer';
declare var CryptoJS: any;
declare var $;
declare var swal;
@Component({
  selector: 'app-wishlist',
  templateUrl: './wishlist.component.html',
  styleUrls: ['./wishlist.component.css']
})
export class WishlistComponent implements OnInit {

  userdata: any;

  constructor(private _global: AppGlobals, private element: ElementRef, private router: Router, private localstorages: Localstorage, private httpservice: HttpServices) {

  }
  
  ngOnInit() {
    this.userdata = this.localstorages.getarrayitems('Userdata')
    var that=this;
    this.httpservice.GetSinglehttp('api/UserWishlist' + '/' + that.userdata[0].UserId)
      .subscribe(
      res => {
        this._global.wishitem = res.Wishlist;
       // console.log(this.wishitem)
      });
  }

  itemdetail(categegoryid, adid, ShopId) {
    alert(categegoryid + " " + adid + ShopId)
    if (ShopId != null && ShopId != undefined) {
      this.httpservice.GetSinglehttp('api/getitemdetail/' + categegoryid + '/' + adid)
        .subscribe(
        ress => {
          this._global.item_detail = ress

          this.router.navigate(["allresult/Product", adid]);
        });
    } else {
      this.httpservice.GetSinglehttp('api/getitemdetailforopenpost/' + categegoryid + '/' + adid)
        .subscribe(
        ress => {
          this._global.item_detail = ress

          this.router.navigate(["allresult/Product", adid]);
        });
    }

  }
  adtocart(ShopID, AdId, Image, Title, Price, CategoryId) {
    if (this.userdata != undefined || this.userdata != null) {


      var AdCart = this.localstorages.getarrayitems("AdCartdata");
      var data = {
        ShopID: ShopID,
        Title: Title,
        Price: Price,
        Image: Image,
        Order_Quantity: 1,
        CardName: 'BUY',
        Confirmitem: true,
        AdIdforcart: AdId,
        UserID: this.userdata[0].UserId,
        CategoryId: CategoryId
      };
      console.log(data)
      if (AdCart == undefined) {
        var datas = [];
        datas.push(data);
        this.localstorages.setarrayitems(datas, 'AdCartdata');
      } else {
        AdCart.push(data);
        this.localstorages.setarrayitems(AdCart, 'AdCartdata');
      }

      swal({
        type: 'success',
        title: 'Added!',
        html: 'Item Added in your Cart!'
      })
      var AdCart = this.localstorages.getarrayitems("AdCartdata");
      console.log(AdCart)
    } else {
      swal(
        'Oops...',
        'Please Login For Shopping!',
        'error'
      )
    }

  }
}



