import { Localstorage } from './../router-guard/StorageService.service';
import { AppComponent } from './../app.component';
import { HttpServices } from './../Services/http.services';
import { Component, OnInit, ElementRef, Input, ViewChild } from '@angular/core';
import { AuthService } from "angular2-social-login";
import { LocalStorageService } from 'angular-2-local-storage';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { FileUploader } from 'ng2-file-upload/ng2-file-upload';
import { Http, Response } from '@angular/http';
import { AppGlobals } from './../app.global';
declare var swal: any;
declare var $: any;
//import the do function to be used with the http library.
import "rxjs/add/operator/do";
//import the map function to be used with the http library
import "rxjs/add/operator/map";
import { HeaderComponent } from "../header/header.component";
@Component({
  selector: 'app-searchproduct',
  templateUrl: './searchproduct.component.html',
  styleUrls: ['./searchproduct.component.css']
})
export class SearchproductComponent implements OnInit {
  abc: any;
  searchitems: any;
  Trendmobileitems: any;
  brand: any;
  constructor(private router: Router, private _global: AppGlobals, private httpservice: HttpServices, private localstorages: Localstorage) {
    this.searchitems = this._global.items;
  }

  ngOnInit() {
    this.searchitems = this.localstorages.getarrayitems("searchitems")
    this._global.items = this.searchitems;

    if (this.searchitems != null || this.searchitems != undefined) {
      this.httpservice.GetSinglehttp('api/getbrands/' + this.searchitems[0].CategoryID)
        .subscribe(
        ress => {
          console.log(ress)
          this._global.brand = ress;
        });
    }


  }
  test(a) {
    console.log(a)
  }
  filterrecordbybrand(CategoryId, keyword) {
    //alert(keyword)
    if (keyword != undefined || keyword != null) {


      this.httpservice.GetSinglehttp('api/recordfilter/' + CategoryId + '/' + keyword)
        .subscribe(
        ress => {
          if (ress != undefined || ress != null) {
            if (ress.length > 0) {
              this._global.items = ress;
              this.localstorages.setarrayitems(ress, 'searchitems');
            } else {
              swal(
                'Oops...',
                "No result Found",
                'error'
              )
              this._global.items = this.localstorages.getarrayitems("searchitems");
            }


          } else {
            swal(
              'Oops...',
              "No result Found",
              'error'
            )
            this._global.items = this.localstorages.getarrayitems("searchitems");
          }

        });
      window.scrollTo(10, 200)

    }
  }
  filterrecord(CategoryId, keyword, e) {

    if (e.target.checked == true) {


      this.httpservice.GetSinglehttp('api/recordfilter/' + CategoryId + '/' + keyword)
        .subscribe(
        ress => {
          if (ress != undefined || ress != null) {
            if (ress.length > 0) {
              this._global.items = ress;
              this.localstorages.setarrayitems(ress, 'searchitems');
            } else {
              swal(
                'Oops...',
                "No result Found",
                'error'
              )
              this._global.items = this.localstorages.getarrayitems("searchitems");
            }


          } else {
            swal(
              'Oops...',
              "No result Found",
              'error'
            )
            this._global.items = this.localstorages.getarrayitems("searchitems");
          }

        });
      window.scrollTo(10, 200)
    } else {
      e.target.checked = false;
      window.scrollTo(10, 200)
    }

  }
  filterrecordbysearch(CategoryId, keyword, e) {

    if (e.target.checked == true) {


      this.httpservice.GetSinglehttp('api/recordfilterbyprice/' + CategoryId + '/' + keyword)
        .subscribe(
        ress => {
          console.log(ress)
          if (ress != undefined || ress != null) {
            if (ress.length > 0) {
              this._global.items = ress;
              this.localstorages.setarrayitems(ress, 'searchitems');
            } else {
              swal(
                'Oops...',
                "No result Found",
                'error'
              )
              this._global.items = this.localstorages.getarrayitems("searchitems");
            }
          } else {
            swal(
              'Oops...',
              "No result Found",
              'error'
            )
            this._global.items = this.localstorages.getarrayitems("searchitems");
          }



        });
      window.scrollTo(10, 200)
    } else {
      e.target.checked = false;
      window.scrollTo(10, 200)
    }

  }

  itemdetail(categegoryid, adid, email) {
   alert(categegoryid + " " + adid + email)
     if (email == null) {
       this.httpservice.GetSinglehttp('api/getitemdetail/' + categegoryid + '/' + adid)
         .subscribe(
         ress => {
           this._global.item_detail = ress;
 console.log(ress)
 this.router.navigate(["allresult/Product", adid]);
         
         });
        
     }else{
       this.httpservice.GetSinglehttp('api/getitemdetailforopenpost/' + categegoryid + '/' + adid)
       .subscribe(
       ress => {
         this._global.item_detail = ress
 
         this.router.navigate(["allresult/Product", adid]);
       });
     }
 
   }
}



