
import { Injectable } from '@angular/core';
import { CanActivate, CanDeactivate, Router } from '@angular/router';
import { Localstorage } from './../router-guard/StorageService.service';
@Injectable()

export class Checkguard implements CanActivate {

    constructor(private localstorages: Localstorage ,private router: Router) {

    }
    canActivate(): boolean {
        var isactive = this.localstorages.getarrayitems("Userdata")
        
        if (isactive != undefined) {
            if (isactive.length > 0) {
                this.router.navigate(['/main']);
                return false;


            } else {
                return true;
            }
        } else {
            return true;
        }


    }

}


