
import { AppComponent } from './../app.component';
import { Component, OnInit, Input } from '@angular/core';
import { Localstorage } from './../router-guard/StorageService.service';
import { HttpServices } from './../Services/http.services';
import { Router } from '@angular/router'; import { Subscription } from "rxjs";
import { TimerObservable } from "rxjs/observable/TimerObservable";
import { AppGlobals } from '../app.global';
declare var swal;
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent extends AppComponent implements OnInit {
  itemsdata: any;
  quickitem: any;
  public subscriptioncat = [];
  busy: Promise<any>;
  public mobileitems = [];
  public motorsitems = [];
  public Trendmotorsitems = [];
  public Trendmobileitems = [];
  public shops = [];
  public subscriptionlist = [];
  public userdata = [];
  myVar: boolean;
  @Input() recievemsg: string;
  constructor(private _global: AppGlobals, private router: Router, private httpservice: HttpServices, private localstorages: Localstorage) {
    super();

  }

  private tick: string;
  private subscription: Subscription;


  ngOnInit() {


    this.httpservice.GetSinglehttp("api/allmobilesitems/New")
      .subscribe(
      ress => {
        // alert(ress)
        this.Trendmobileitems = ress;
      });

    let timer = TimerObservable.create(2000, 1000);
    this.subscription = timer.subscribe(t => {

      t = t + 1;

      if (t == 1) {

        if (this.userdata == undefined) {
          UserId = 0;
        } else {
          UserId = this.userdata[0].UserId;
        }


        this.httpservice.GetSinglehttp("api/Homepagedata/" + UserId)
          .subscribe(
          res => {
            console.log(res)
            this.mobileitems = res.Mobile;

            this.motorsitems = res.Motor;
            this.localstorages.setarrayitems(res.Shop, 'subcribershoped');
            //  console.log(res)
            // if (that.userdata != undefined) {
            //   that.httpservice.GetSinglehttpwithtoken("api/usersubscriptions/" + that.userdata[0].UserId)
            //     .subscribe(
            //     ressss => {
            //       //  console.log(ressss)
            //       this.subscriptionlist = ressss.subscription;
            //     });



            // }
            this.httpservice.GetSinglehttp("api/allmotorsitems/New")
              .subscribe(
              res => {

                this.Trendmotorsitems = res;


              });

          })


        this.subscription.unsubscribe();


        document.getElementById("loading12").style.display = "none";

      }


    });
    var that = this;
    var UserId: any;
    this.userdata = this.localstorages.getarrayitems("Userdata");




  }
  itemdetail(categegoryid, adid, email) {
    // alert(categegoryid + " " + adid + email)
    if (email == null) {
      this.httpservice.GetSinglehttp('api/getitemdetail/' + categegoryid + '/' + adid)
        .subscribe(
        ress => {
          this._global.item_detail = ress

          this.router.navigate(["allresult/Product", adid]);
        });
    } else {
      this.httpservice.GetSinglehttp('api/getitemdetailforopenpost/' + categegoryid + '/' + adid)
        .subscribe(
        ress => {
          this._global.item_detail = ress

          this.router.navigate(["allresult/Product", adid]);
        });
    }

  }
  getshopcatitems(ShopId, CategoryId) {
    console.log(ShopId + "  " + CategoryId)
    this.httpservice.GetSinglehttpwithtoken("api/activeallShopcatitems/" + ShopId + "/" + CategoryId)
      .subscribe(
      ress => {
        //   console.log(ress)
      })


  }
  unsubscribe(SubscriptionId) {
    var that = this;
    console.log(SubscriptionId)
    var data = { SubscriptionId: SubscriptionId }
    this.httpservice.postSinglehttpwithtonken("api/UnsubscribeShop", data).subscribe(
      res => {
        // console.log(res);
        this.userdata = this.localstorages.getarrayitems("Userdata");
        if (this.userdata != undefined) {
          that.httpservice.GetSinglehttpwithtoken("api/usersubscriptions/" + that.userdata[0].UserId)
            .subscribe(
            ressss => {
              that.subscriptionlist = ressss.subscription;
              // this.subscriptionlist = ressss.subscription;
              that.httpservice.GetSinglehttp("api/GetShops/" + that.userdata[0].UserId)
                .subscribe(
                resss => {
                  this.shops = resss;
                  //  console.log(resss)
                })




            })
        }
      });
  }
  subscribe(ShopId) {
    var that = this;
    var userdata = this.localstorages.getarrayitems("Userdata");

    if (userdata != undefined) {
      var postdata = { ShopID: ShopId, UserID: userdata[0].UserId };
      this.httpservice.postSinglehttpwithtonken("api/SubscribeShop", postdata).subscribe(
        res => {
          that.userdata = that.localstorages.getarrayitems("Userdata");
          if (this.userdata != undefined) {
            //   console.log(this.userdata[0].UserId)
            that.httpservice.GetSinglehttpwithtoken("api/usersubscriptions/" + that.userdata[0].UserId)
              .subscribe(
              ressss => {

                that.subscriptionlist = ressss.subscription;
                // console.log(this.subscriptionlist.length)
                that.httpservice.GetSinglehttp("api/GetShops/" + that.userdata[0].UserId)
                  .subscribe(
                  resss => {
                    that.shops = resss;
                    //console.log(resss)
                  })




              })
          }

        })
    } else {
      //alert("Please Login for Subscribtion");

    }
  }
  toprecordmotors() {
    this.httpservice.GetSinglehttp("api/allmotorsitems")
      .subscribe(
      res => {

        this.motorsitems = res;
      });
  }

  getmotors(Condition) {

    this.httpservice.GetSinglehttp("api/allmotorsitems/" + Condition)
      .subscribe(
      res => {

        this.motorsitems = res;
      });

  }
  toprecordmobiles() {
    this.httpservice.GetSinglehttp("api/allmobilesitems")
      .subscribe(
      res => {

        this.mobileitems = res;
      });
  }

  getmobiles(Condition) {



    this.httpservice.GetSinglehttp("api/allmobilesitems/" + Condition)
      .subscribe(
      res => {

        this.mobileitems = res;
      });

  }


  quickviewitem(Condition, AdId) {
   // alert(AdId)
    // this.quickitem = null;
    var that = this;
    that.httpservice.GetSinglehttp("api/allitems/" + Condition + "/" + AdId)
      .subscribe(
      res => {

        that.quickitem = res;
      });
  }

  adtocart(ShopID, AdId, Email, Image, Title, Price, CategoryId) {
    if (this.userdata != undefined || this.userdata != null) {


      var AdCart = this.localstorages.getarrayitems("AdCartdata");
      var data = {
        ShopID: ShopID,
        Email: Email,
        Title: Title,
        Price: Price,
        Image: Image,
        Order_Quantity: 1,
        CardName: 'BUY',
        Confirmitem: true,
        AdIdforcart: AdId,
        UserID: this.userdata[0].UserId,
        CategoryId: CategoryId
      };
      console.log(data)
      if (AdCart == undefined) {
        var datas = [];
        datas.push(data);
        this.localstorages.setarrayitems(datas, 'AdCartdata');
      } else {
        AdCart.push(data);
        this.localstorages.setarrayitems(AdCart, 'AdCartdata');
      }

      swal({
        type: 'success',
        title: 'Added!',
        html: 'Item Added in your Cart!'
      })
      var AdCart = this.localstorages.getarrayitems("AdCartdata");
      console.log(AdCart)
    } else {
      swal(
        'Oops...',
        'Please Login For Shopping!',
        'error'
      )
    }

  }






  Adtowish(ShopID, AdId, Email, Image, Title, Price, CategoryId, description) {

    if (this.userdata != undefined || this.userdata != null) {
      // var Adwish = this.localstorages.getarrayitems("AdWishdata");
      var data = [];
      data = [{
        ShopID: ShopID,
        Email: Email,
        Title: Title,
        Price: Price,
        Image: Image,
        description: description,
        AdIdforcart: AdId,
        UserID: this.userdata[0].UserId,
        CategoryId: CategoryId
      }]
      console.log(data)

      this.httpservice.Posthttp("api/Adwishitem", data)
        .subscribe(res => {
          console.log(res)

          swal({
            type: 'success',
            title: 'Added!',
            html: 'Your selected item in your Wish List'
          })


        });
      // if (Adwish == undefined) {
      //   var datas = [];
      //   datas.push(data);
      //   this.localstorages.setarrayitems(datas, 'AdWishdata');
      // } else {
      //   Adwish.push(data);
      //   this.localstorages.setarrayitems(Adwish, 'AdWishdata');
      // }

      // swal({
      //   type: 'success',
      //   title: 'Added!',
      //   html: 'Item Added in your WishList!'
      // })
      // var Adwish = this.localstorages.getarrayitems("AdWishdata");
      // console.log(Adwish)
    } else {
      swal(
        'Oops...',
        'Please Login For Wishlist!',
        'error'
      )
    }

  }
}



