import { AppGlobals } from './app.global';

import { Localstorage } from './router-guard/StorageService.service';
import { Checkguard } from './router-guard/checkguard';
import { HttpServices } from './Services/http.services';

//Import all Libraries here
import { HttpModule, Http } from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { RouterModule } from '@angular/router';
import { Angular2SocialLoginModule } from "angular2-social-login";
import { LocalStorageModule } from 'angular-2-local-storage';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { FileSelectDirective } from 'ng2-file-upload';

//Import all Components here 
import { AppComponent } from './app.component';
import { RegistrationComponent } from './registration/registration.component';
import { LoginComponent } from './login/login.component';
import { MyAccountComponent } from './my-account/my-account.component';
import { HomeComponent } from './home/home.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { SearchBarComponent } from './search-bar/search-bar.component';
import { NavBarComponent } from './nav-bar/nav-bar.component';
import { MainPageComponent } from './main-page/main-page.component';
import { PostingComponent } from './posting/posting.component';
import { SliderComponent } from './slider/slider.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { CreateShopComponent } from './create-shop/create-shop.component';
import { MyshopsComponent } from './myshops/myshops.component';
import { PagenotfoundComponent } from './pagenotfound/pagenotfound.component';
import { Ng2CompleterModule } from "ng2-completer";


import { MobilecontrolComponent } from './mobilecontrol/mobilecontrol.component';
import { ShopitemsComponent } from './shopitems/shopitems.component';
import { MotorcontrolComponent } from './motorcontrol/motorcontrol.component';
import { NoaccountComponent } from './noaccount/noaccount.component';
import { TestComponent } from './test/test.component';
import { AutocompComponent } from './autocomp/autocomp.component';
import { SearchproductComponent } from './searchproduct/searchproduct.component';
import { AllresultComponent } from './allresult/allresult.component';
import { ItemdetailComponent } from './itemdetail/itemdetail.component';
import { AccountsettingComponent } from './accountsetting/accountsetting.component';
import { AllitemsComponent } from './allitems/allitems.component';
import {Ng2PaginationModule} from 'ng2-pagination';
import { ShopAdsComponent } from './shop-ads/shop-ads.component';
import { TestingComponent } from './testing/testing.component';
import { AdcartComponent } from './adcart/adcart.component';
import { ReceiveorderComponent } from './receiveorder/receiveorder.component';
import { WishlistComponent } from './wishlist/wishlist.component';

export function HttpLoaderFactory(http: Http) {

  return new TranslateHttpLoader(http, 'assets/i18n/', '.json');
}
let providers = {
  "google": {
    "clientId": "176580230944-g36qn1qv61l24t1ke1ogs0kgh576gdch.apps.googleusercontent.com"
  }
  // "linkedin": {
  //   "clientId": "LINKEDIN_CLIENT_ID"
  // },
  // "facebook": {
  //   "clientId": "FACEBOOK_CLIENT_ID",
  //   "apiVersion": "<version>" //like v2.4 
  // }
};

@NgModule({
  declarations: [
    AppComponent,
    RegistrationComponent,
    LoginComponent,
    MyAccountComponent,
    FileSelectDirective,
    HomeComponent,
    HeaderComponent,
    FooterComponent,
    SearchBarComponent,
    NavBarComponent,
    MainPageComponent,
    PostingComponent,
    SliderComponent,
    SidebarComponent,
    CreateShopComponent,
    MyshopsComponent,
    PagenotfoundComponent,

    MobilecontrolComponent,

    ShopitemsComponent,

    MotorcontrolComponent,

    NoaccountComponent,

    TestComponent,

    AutocompComponent,

    SearchproductComponent,

    AllresultComponent,

    ItemdetailComponent,

    AccountsettingComponent,

    AllitemsComponent,

    ShopAdsComponent,

    TestingComponent,

    AdcartComponent,

    ReceiveorderComponent,

    WishlistComponent,


  ],
  imports: [
    Ng2PaginationModule,
    Ng2CompleterModule,
    BrowserModule,
    FormsModule,
    HttpModule,
   
    Angular2SocialLoginModule,


    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [Http]
      }
    }),
    LocalStorageModule.withConfig({
      prefix: 'my-app',
      storageType: 'localStorage'
    }),
    RouterModule.forRoot([
      {
        path: '', component: MainPageComponent, pathMatch: 'full'
      },
     
      {
        path: 'allresult', component: AllresultComponent,

        children: [
          { path: 'allitems', component: AllitemsComponent},
          { path: 'shopads', component: ShopAdsComponent },
          { path: 'wishlist', component: WishlistComponent },
          { path: 'adcart', component: AdcartComponent },
          { path: 'testing', component: TestingComponent },
          {
            path: 'search', component: SearchproductComponent

          },
        
        ]

      },
      { path: "allresult/Product/:AdId", component: ItemdetailComponent },
      {
        path: 'main', component: MainPageComponent
      },
      // { path: 'itemdetail', component: ItemdetailComponent },
      { path: 'login', component: LoginComponent, canActivate: [Checkguard] },
    
      { path: 'posting', component: PostingComponent },
      { path: 'registraion', component: RegistrationComponent, canActivate: [Checkguard] },
      {
        path: 'myAccount', component: MyAccountComponent,

        children: [
          { path: 'myshops', component: MyshopsComponent },
          { path: 'accountsetting', component: AccountsettingComponent },
          { path: 'shopitems', component: ShopitemsComponent },
          { path: 'orders', component: ReceiveorderComponent }
        ]
      },
      { path: 'Home', component: HomeComponent },
      { path: 'createshop', component: CreateShopComponent },
      { path: 'noaccount', component: NoaccountComponent },
      { path: '**', component: PagenotfoundComponent },
    ])
  ],

  providers: [HttpServices, Checkguard, Localstorage, AppGlobals],
  bootstrap: [AppComponent]
})

export class AppModule { }
Angular2SocialLoginModule.loadProvidersScripts(providers);


