import { HttpServices } from './../Services/http.services';
import { Component, OnInit, ElementRef } from '@angular/core';

@Component({
  selector: 'app-autocomp',
  templateUrl: './autocomp.component.html',
  styleUrls: ['./autocomp.component.css'],

})
export class AutocompComponent implements OnInit {
  Trendmobileitems: any;

  jdata: any;


  public query = '';
  // public countries = [ "Albania","Andorra","Armenia","Austria","Azerbaijan","Belarus",
  //                     "Belgium","Bosnia & Herzegovina","Bulgaria","Croatia","Cyprus",
  //                     "Czech Republic","Denmark","Estonia","Finland","France","Georgia",
  //                     "Germany","Greece","Hungary","Iceland","Ireland","Italy","Kosovo",
  //                     "Latvia","Liechtenstein","Lithuania","Luxembourg","Macedonia","Malta",
  //                     "Moldova","Monaco","Montenegro","Netherlands","Norway","Poland",
  //                     "Portugal","Romania","Russia","San Marino","Serbia","Slovakia","Slovenia",
  //                     "Spain","Sweden","Switzerland","Turkey","Ukraine","United Kingdom","Vatican City"];
  public countries = [
    { name: "mzu", age: "23" },
    { name: "mzu", age: "23" },
  ]


  public filteredList = [];
  public elementRef;

  constructor(myElement: ElementRef, private httpservice: HttpServices) {
    this.elementRef = myElement;
  }

  ngOnInit() {

    this.httpservice.GetSinglehttp("api/allmobilesitems/New")
      .subscribe(
      ress => {

        this.Trendmobileitems = ress;
        console.log(ress)
      });
  }


}



