import { PagenotfoundComponent } from './../pagenotfound/pagenotfound.component';
import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { Localstorage } from './../router-guard/StorageService.service';
@Injectable()

export class HttpServices {

    public url: string = 'http://192.168.15.32:8081/';
   
    constructor(private localstorages: Localstorage, private http: Http) { }


    //No Parameter route,For Open Route.
    public GetSinglehttp(service) {

        return this.http.get(this.url + service)
            .map((respose) => respose.json());
    }
    //Taking Parameter route,For Open Route.
    public Gethttpwithparam(service, data) {

        return this.http.get(this.url + service, data)
            .map((respose) => respose.json());

    }
    //No Parameter route,For close Route.
    public GetSinglehttpwithtoken(service) {
        let head = new Headers();
        // head.append('token', localStorage.getItem("TOKEN"));
        head.append('token', JSON.parse(this.localstorages.getsingleitems("TOKEN")));
        return this.http.get(this.url + service, { "headers": head })
            .map((respose) => respose.json());
    }
    //Taking Parameter route,For close Route.
    public GetSinglehttpwithparamtoken(service, data) {
        let head = new Headers();
        // head.append('token', localStorage.getItem("TOKEN"));
        head.append('token', JSON.parse(this.localstorages.getsingleitems("TOKEN")));
        return this.http.get(this.url + service, data)
            .map((respose) => respose.json());
    }



    public postSinglehttpwithtonken(service, data) {
        let head = new Headers();
        // head.append('token', localStorage.getItem("TOKEN"));
        head.append('token', JSON.parse(this.localstorages.getsingleitems("TOKEN")));
        return this.http.post(this.url + service, data, {
            headers: head
        })
            .map((respose) => respose.json());



    }
    private handleError(error: Response) {
        console.error(error);
        return Observable.throw(error);

    }
    public Posthttp(method, data) {

        return this.http.post(this.url + method, data)
            .map((respose) => respose.json())
            .catch(this.handleError)

    }
//  public Posthttpwithourdata(method) {

//         return this.http.post(this.url + method,)
//             .map((respose) => respose.json())
//             .catch(this.handleError)

//     }

}


