import { Router } from '@angular/router';
import { Localstorage } from './../router-guard/StorageService.service';
import { Component, OnInit, Input, ElementRef, EventEmitter } from '@angular/core';
import { HttpServices } from '../Services/http.services';
declare var swal:any;
@Component({
  selector: 'app-accountsetting',
  templateUrl: './accountsetting.component.html',
  styleUrls: ['./accountsetting.component.css']
})
export class AccountsettingComponent implements OnInit {
  address: any;
  UserEmail: string;
  UserMobile: string;
  Images: any;
  UserName: string;
  UserId: any;
  filesToUpload: Array<File>;
  constructor(private router: Router,private element: ElementRef,public httpservice: HttpServices, private localstorages: Localstorage ) {

    this.filesToUpload = [];
  }
  ngOnInit() {
    var userdata = this.localstorages.getarrayitems('Userdata');
    this.UserId = userdata[0].UserId
    console.log(userdata)
    this.UserName = userdata[0].Name;


    var that = this;
    if (userdata != null || userdata != undefined) {

      that.Images = "http://localhost:8081/api/GetUserProfileimage/UserProfilePic-" + userdata[0].UserId + ".png";

    }


    this.UserMobile = userdata[0].Phone_no;
    this.UserEmail = userdata[0].email;
  }

  UpdateUserAccount(data) {
    console.log(data);

    var Userinfo = { Name: data.UserName, Phone_no: data.UserMobile, NewPassword: data.newpassword, CurrentPassword: data.currentpassword }
    this.httpservice.Posthttp("api/UpdateUserInfo/" + this.UserId, Userinfo).subscribe(
      res => {

        this.localstorages.setarrayitems(res.userData, "Userdata");
        swal({
          type: 'success',
          title: 'Success!',
          html: 'User Profile Update Successfully!'
        });
        this.router.navigate(["main"]);
      })
  }

  changeListner(event) {
    var reader = new FileReader();
    var image = this.element.nativeElement.querySelector('.image');

    reader.onload = function (e: any) {
      var src = e.target.result;
      image.src = src;
    };
console.log(event)
    reader.readAsDataURL(event.target.files[0]);
  }
  setDefaultPic() {
    this.Images = "/assets/more_assets/images/ad3.jpg";
  }

}



