
import { AppComponent } from './../app.component';
import { Component, OnInit, Input } from '@angular/core';
import { Localstorage } from './../router-guard/StorageService.service';
import { HttpServices } from './../Services/http.services';
import { Router } from '@angular/router'; import { Subscription } from "rxjs";
import { TimerObservable } from "rxjs/observable/TimerObservable";
import { AppGlobals } from '../app.global';


@Component({
  selector: 'main-slider-bar',
  templateUrl: './slider.component.html',
  styleUrls: ['./slider.component.css']
})
export class SliderComponent implements OnInit {

  constructor(private _global: AppGlobals, private router: Router, private httpservice: HttpServices, private localstorages: Localstorage) { }

  ngOnInit() {
  }
  allproductitems(categoryId) {
    alert(categoryId)
    this.httpservice.GetSinglehttp("api/allitems/" + categoryId)
      .subscribe(
      ress => {
        this._global.items = ress;
        this.localstorages.setarrayitems(ress, 'allitems');
      });
      this.router.navigate(["/allresult/allitems"]);
  }
}



