import { Localstorage } from './../router-guard/StorageService.service';
import { AppComponent } from './../app.component';
import { HttpServices } from './../Services/http.services';
import { Component, OnInit, ElementRef, Input, ViewChild } from '@angular/core';
import { AuthService } from "angular2-social-login";
import { LocalStorageService } from 'angular-2-local-storage';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { FileUploader } from 'ng2-file-upload/ng2-file-upload';
import { Http, Response } from '@angular/http';
//import the do function to be used with the http library.
import "rxjs/add/operator/do";
//import the map function to be used with the http library
import "rxjs/add/operator/map";
import { HeaderComponent } from "../header/header.component";
const URL = 'http://localhost:8081/api/uploads';
declare var swal: any;
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers: [HttpServices],

})


export class LoginComponent extends AppComponent implements OnInit {

  errormsg: any;
  sub: any;
  user: any = [];
  flag: any = 0;




  constructor(private localstorages: Localstorage, private http: Http, private el: ElementRef, private translate: TranslateService, private router: Router, private localStorageService: LocalStorageService, public _auth: AuthService, public httpservice: HttpServices) {
    super();
    translate.addLangs(['en', 'ur']);
    translate.setDefaultLang('en');

    let browserlang = translate.getBrowserLang();
    translate.use('en')


  }

  ngOnInit() {

  }
  // public navJson: any = {
  //   "Login": false,
  //   "Registration": true,
  //   "SubmitAd":true,
  //   "CreateShop": false
  // }
  SignUp(userdata) {

    var member = {
      email
      :
      userdata[0].email,
      gender
      :
      "None",
      name
      :
      userdata[0].name,
      password
      :
      "None",
      phone_no
      :
      "None",
      cityID
      :
      "1"
    }

    this.httpservice.Posthttp("api/SignUp", member)
      .subscribe(
      res => {

        console.log(res);


      }
      )
  }


  SignUpbygoogle(provider) {
    this.sub = this._auth.login(provider).subscribe(
      (data) => {

        this.user = data;

        console.log(this.user.email);
        var that = this;
        var member = { Email: this.user.email };
        this.httpservice.Posthttp("api/UserIdentification", member)
          .subscribe(
          res => {

        //   alert(res);

            if (res == 1) {
              console.log("Go to Home");
              var member = { email: this.user.email };
              this.httpservice.Posthttp("api/SignInbygoogle", member).subscribe(function (res) {
                console.log(res)
                console.log(res.userData[0].email)

                // localStorage.setItem("Userdata", JSON.stringify(res.userData));
                that.localstorages.setarrayitems(res.userData, "Userdata");
                if (res.VendorId != undefined) {
                  // localStorage.setItem("VendorId", res.VendorId);
                  that.localstorages.setsingleitems(res.VendorId, "VendorId");
                }
                // localStorage.setItem("IsVendor", res.Isvendor);
                // localStorage.setItem("TOKEN", res.token);
                that.localstorages.setsingleitems(res.Isvendor, "IsVendor");
                that.localstorages.setsingleitems(res.token, "TOKEN");





              });



             that.router.navigate(['/myAccount']);
            } else {
              this.localstorages.setarrayitems([this.user], "Userdata");
              var userdata = that.localstorages.getarrayitems("Userdata");
              this.SignUp(userdata)
              console.log("Not Exists");
              this.router.navigate(['/myAccount']);
            }

          }
          )

        // localStorage.setItem("Userdatafromgoogle", JSON.stringify([this.user]));
        //user data 
        //name, image, uid, provider, uid, email, token (returns tokenId for google, accessToken for Facebook, no token for linkedIn) 
      }
    )
  }

  public users: string;
  Signin(user: any) {

    var that = this;
    this.users = user.email;
    var member = { email: user.email, password: user.password };
    this.httpservice.Posthttp("api/login", member)
      .subscribe(res => {
        console.log(res)
        this.flag = 1;
        //store data on client side
        if (res.VendorId != undefined && res.ShopId != undefined) {
          this.localstorages.setsingleitems(res.VendorId, "VendorId");
          this.localstorages.setsingleitems(res.ShopId, "ShopId");
        }
        this.localstorages.setsingleitems(res.Isvendor, "IsVendor");
        this.localstorages.setsingleitems(res.token, "TOKEN");
        this.localstorages.setarrayitems(res.userData, "Userdata");
        sessionStorage.useremail = user.email;

        
        that.router.navigate(['/main']);
      }, reserror => {
        var error = JSON.parse(reserror._body);
        this.errormsg = error.message;
        swal(
          'Oops...',
          this.errormsg,
          'error'
        )



      })


  }
  opensweetalert() {
    var that = this;
    swal({
      title: "You don't have account but placed an Ad?",
      input: 'email',
      html: "Please provide the e-mail address  used in the Ad. We will send you a link in e-mail/sms that will allow you to access your account and manage your Ads.",
      showCancelButton: true,

      confirmButtonText: 'Submit',
      showLoaderOnConfirm: true,
      preConfirm: function (email) {
        return new Promise(function (resolve, reject) {
          setTimeout(function () {
            var a = 1;

            if (email == undefined) {
              reject('Email not defined')
            } else {
              resolve()
              //this.sendmail(email)
            }
          }, 2000)
        })
      },
      allowOutsideClick: false
    }).then(function (email) {

      var v_code = Math.floor((Math.random() * 10000) + 1);

      var data = { Email: email, verificationcode: v_code };
      that.httpservice.Posthttp("api/SendEmail", data).subscribe(function (res) {
        swal({
          type: 'success',
          title: 'Verification Code!',
          html: 'Verification Code send to your mail: ' + email
        })
      });



    })

  }
  sendmail(email) {
    var v_code = Math.floor((Math.random() * 10000) + 1);

    var data = { Email: email, verificationcode: v_code };
    this.httpservice.Posthttp("api/SendEmail", data).subscribe(
      res => {
        swal({
          type: 'success',
          title: 'Verification Code!',
          html: 'Verification Code send to your mail: ' + email
        })
      });
  }
}



