import { HttpServices } from './../Services/http.services';
import { AppComponent } from './../app.component';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Localstorage } from './../router-guard/StorageService.service';
declare var swal: any;
@Component({
  selector: 'app-create-shop',
  templateUrl: './create-shop.component.html',
  styleUrls: ['./create-shop.component.css']
})
export class CreateShopComponent extends AppComponent implements OnInit {

  public Categories = [];

  constructor(private localstorages: Localstorage, private router: Router, private httpservice: HttpServices) {
    super();
  }

  public selectedcat = [];
  ngOnInit() {
    var userdata = this.localstorages.getarrayitems("Userdata")
    
    
     if (userdata == null || userdata == undefined) {
      this.router.navigate(['/login']);
     }

    this.httpservice.GetSinglehttp("api/itemcategories")
      .subscribe(
      res => {
        this.Categories = res;
        // console.log(res)
      }
      )
  }

  createshop(data: any) {
    this.selectedcat = data.itemcategories;
    console.log(data)
    var userdata = this.localstorages.getarrayitems("Userdata")
    console.log(userdata)
    var member = { nic: data.nic, shop_name: data.shopname, userid: userdata[0].UserId }
    this.httpservice.postSinglehttpwithtonken("api/Vendoraccount", member).subscribe(
      res => {
        console.log(res);
        if (res.Isvendor == 1) {
          console.log(res.Isvendor)
          this.localstorages.setsingleitems(res.Isvendor, "IsVendor");
          this.localstorages.setsingleitems(res.vendorId, "VendorId");
          this.localstorages.setsingleitems(res.shopId, "ShopId");
        }
        var members = { ShopId: res.shopId, CategoryId: data.itemcategories }
        console.log(members)
        this.httpservice.postSinglehttpwithtonken("api/Shopcategories", members).subscribe(
          res => {
            swal(
              'Good job!',
              'You have now your own Shop!',
              'success'
            )
            this.router.navigate(['/main']);
          })



      })

    // var members = { ShopId: 2, CategoryId: data.itemcategories }
    //     console.log(members)
    //     this.httpservice.postSinglehttpwithtonken("api/Shopcategories", members).subscribe(
    //       res => {
    //         console.log("success!");
    //         this.router.navigate(['/main']);
    //       })
  


  }

}



