import { Localstorage } from './../router-guard/StorageService.service';
import { AppComponent } from './../app.component';
import { HttpServices } from './../Services/http.services';
import { Component, OnInit, ElementRef, Input, ViewChild } from '@angular/core';
import { AuthService } from "angular2-social-login";
import { LocalStorageService } from 'angular-2-local-storage';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { FileUploader } from 'ng2-file-upload/ng2-file-upload';
import { Http, Response } from '@angular/http';
import { AppGlobals } from './../app.global';
declare var swal: any;
declare var $: any;


@Component({
  selector: 'app-shop-ads',
  templateUrl: './shop-ads.component.html',
  styleUrls: ['./shop-ads.component.css']
})
export class ShopAdsComponent implements OnInit {
  allitems: any=[];
  shopname: string;
  ShopOwnerName: string;
  ShopOwnerNumber: string;
  ShopOwnerEmail: string;
  ShopOwnerImage: string;
  subscribes: boolean;
  ShopId: any;
  constructor(private router: Router, private _global: AppGlobals, private httpservice: HttpServices, private localstorages: Localstorage) {

     //this.allitems = this._global.items;

  }

  ngOnInit() {
    this.allitems = this._global.items;
    this.ShopId = this.allitems[0].ShopID;
    this.shopname = this.allitems[0].ShopName;
    this.ShopOwnerName = this.allitems[0].Name;
    this.ShopOwnerNumber = this.allitems[0].Phone_no;
    this.ShopOwnerEmail = this.allitems[0].Email;
    this.ShopOwnerImage = "UserProfilePic-" + this.allitems[0].UserId + ".png";

    this.checksubscription(this.allitems[0].ShopID);
  }
  checksubscription(ShopId) {
    var userdata = this.localstorages.getarrayitems("Userdata")
    var shops = this.localstorages.getarrayitems("subcribershoped");
    if (userdata != null && shops != null) {
      for (var i = 0; i < shops.length; i++) {
        if (shops[i].ShopID == ShopId) {
          if (shops[i].UserID == userdata[0].UserId) {
            // alert("true")
            this.subscribes = true;
          } else {
            //alert("false")
            this.subscribes = false;
          }
          break;
        }
      }
    } else {
      this.subscribes = false;
    }

  }
  itemdetail(categegoryid, adid, email) {
    //alert(email)
    if (email != null) {
      this.httpservice.GetSinglehttp('api/getitemdetail/' + categegoryid + '/' + adid)
        .subscribe(
        ress => {
          this._global.item_detail = ress

          this.router.navigate(["allresult/Product", adid]);
        });
    } else {
      this.httpservice.GetSinglehttp('api/getitemdetailforopenpost/' + categegoryid + '/' + adid)
        .subscribe(
        ress => {
          this._global.item_detail = ress

          this.router.navigate(["allresult/Product", adid]);
        });
    }

  }
  subscribe(ShopId) {
    //alert(ShopId)
    var userdata = this.localstorages.getarrayitems("Userdata")
    if (userdata != null) {
      var postdata = { ShopID: ShopId, UserID: userdata[0].UserId };
      this.httpservice.postSinglehttpwithtonken("api/SubscribeShop", postdata)
        .subscribe(
        ressss => {
          this.subscribes = true;
          this.httpservice.GetSinglehttp("api/GetShops/" + userdata[0].UserId)
            .subscribe(
            resss => {
              this.localstorages.setarrayitems(resss, 'subcribershoped');
            })
        });

    } else {
      swal(
        'Oops...',
        'You have to login for Subscription!',
        'error'
      )
      this.router.navigate(['/login']);

    }
  }


  adtocart(ShopID, AdId, Email, Image, Title, Price, CategoryId) {
    var userdata = this.localstorages.getarrayitems("Userdata")
    if (userdata != undefined || userdata != null) {


      var AdCart = this.localstorages.getarrayitems("AdCartdata");
      var data = {
        ShopID: ShopID,
        Email: Email,
        Title: Title,
        Price: Price,
        Image: Image,
        Order_Quantity: 1,
        CardName: 'BUY',
        Confirmitem: true,
        AdIdforcart: AdId,
        UserID: userdata[0].UserId,
        CategoryId: CategoryId
      };
      console.log(data)
      if (AdCart == undefined) {
        var datas = [];
        datas.push(data);
        this.localstorages.setarrayitems(datas, 'AdCartdata');
      } else {
        AdCart.push(data);
        this.localstorages.setarrayitems(AdCart, 'AdCartdata');
      }

      swal({
        type: 'success',
        title: 'Added!',
        html: 'Item Added in your Cart!'
      })
      var AdCart = this.localstorages.getarrayitems("AdCartdata");
      console.log(AdCart)
    } else {
      swal(
        'Oops...',
        'Please Login For Shopping!',
        'error'
      )
    }

  }
}



