import { AppGlobals } from './../app.global';
import { MyshopsComponent } from './../myshops/myshops.component';
import { Component, OnInit } from '@angular/core';
import { Localstorage } from './../router-guard/StorageService.service';
import { HttpServices } from './../Services/http.services';
import { Router } from '@angular/router';
declare var jQuery: any;
declare var swal: any;
@Component({
  selector: 'app-shopitems',
  templateUrl: './shopitems.component.html',
  styleUrls: ['./shopitems.component.css']
})
export class ShopitemsComponent implements OnInit {

  public shopitems = []


  public categorieslist = [];
  message: string;
  constructor(private _global: AppGlobals, private router: Router, private httpservice: HttpServices, private localstorages: Localstorage) { }



  ngOnInit() {
    this.shopitems = this.localstorages.getarrayitems("shopitems");

    if (this.shopitems.length > 0) {
      var data = { CategoryID: this.shopitems[0].CategoryID };
      this.httpservice.GetSinglehttpwithtoken("api/getcategories/" + this.shopitems[0].CategoryID)
        .subscribe(
        res => {
          this.categorieslist = res;
        })

    } else {
      swal(
        'Oops...',
        this.message,
        'error'
      )
      this.message = "You have no items";

    }


  }

 

  activateitem(shopid, categoryid, AdId) {
    var data;
    var userdata = this.localstorages.getarrayitems("Userdata")
    if (categoryid == 1) {
      data = { IsActive: 1, MobileAdsId: AdId };
    }
    else if (categoryid == 2) {

    }
    else if (categoryid == 3) {
      data = { IsActive: 1, MotorAdsId: AdId };
    }
    console.log(shopid + "  " + categoryid + "  " + AdId)

    this.httpservice.postSinglehttpwithtonken("api/actdeactshopitems/" + categoryid, data).subscribe(
      res => {
        console.log("success!");
        if (shopid == undefined) {
          shopid = this.localstorages.getsingleitems("ShopId");
        }
        this.httpservice.GetSinglehttpwithtoken("api/deactiveallShopcatitems/" + shopid + "/" + categoryid + "/" + userdata[0].UserId)
          .subscribe(
          ress => {
            //  console.log(ress.length)
            if (ress.length > 0) {
              this._global.shopitems = ress;
              this.localstorages.setarrayitems(ress, "shopitems");
              this.shopitems = this.localstorages.getarrayitems("shopitems");


            } else {
              this.message = "You have no deactivate items ";
              //localStorage.removeItem("shopitems");
              this._global.shopitems = undefined;
              swal(
                'Oops...',
                this.message ,
                'error'
              )
            }

          })
      })
  }

  deactivateitem(shopid, categoryid, AdId) {
    var userdata = this.localstorages.getarrayitems("Userdata")
    console.log(categoryid)
    var data;
    var that = this;
    if (categoryid == 1) {
      data = { IsActive: 0, MobileAdsId: AdId };
    }
    else if (categoryid == 2) {

    }
    else if (categoryid == 3) {
      data = { IsActive: 0, MotorAdsId: AdId };
    }
    console.log(shopid)
    this.httpservice.postSinglehttpwithtonken("api/actdeactshopitems/" + categoryid, data).subscribe(
      res => {
        console.log("success!");
        if (shopid == undefined) {
          shopid = this.localstorages.getsingleitems("ShopId");
        }
        this.httpservice.GetSinglehttpwithtoken("api/activeallShopcatitems/" + shopid + "/" + categoryid + "/" + userdata[0].UserId)
          .subscribe(
          ress => {
            if (ress.length > 0) {
              this._global.shopitems = ress;
              this.localstorages.setarrayitems(ress, "shopitems");
              this.shopitems = that.localstorages.getarrayitems("shopitems");
              console.log(that.shopitems)
            }
            else {
              this.message = "You have no deactivate items ";
              //localStorage.removeItem("shopitems");
              this._global.shopitems = undefined;
              swal(
                'Oops...',
                this.message,
                'error'
              )
            }
          })
      })
  }
  itemdetail(categegoryid, adid) {


    this.httpservice.GetSinglehttp('api/getitemdetail/' + categegoryid + '/' + adid)
      .subscribe(
      ress => {
        this._global.item_detail = ress

        this.router.navigate(["allresult/Product", adid]);
      });

  }


}



